import echarts from "echarts";
import {reportData} from '../../server/report'
export default {
  name: "eastChart",
  data() {
    return {
      dataName:[],
      valueList:[],
      nameList:[],
      totalList:[],
      nameLists:[],
      title:[],
      titles:[]
    };
  },
  created(){
    this.getData()
  },
  mounted() {
    let that = this;
      window.onresize = function() {
          this.myChart.resize()
          this.myChartPersend.resize()
      }
  },
  methods: {
    getData(){
      reportData({
        dateType:'D'
      }).then(e=>{
        if(e.data.code==200){
          this.dataName=e.data.data
          for (var key in this.dataName) {
              this.title.push(key)
              this.valueList.push(this.dataName[key])
          }
          this.titles=this.title[1].split('-')
          this.valueList[1].map(item => this.nameList.push(item.organizationName))
          this.valueList[1].map(item=> this.totalList.push(item.total))
          this.$nextTick(()=> {
              this.loadEchart()
            //   this.percentEchart()
          })
          
        }
      })
    },
    //柱状图汇总
    loadEchart() {
        this.myChart = echarts.init(document.getElementById("chart_example"));
        this.myChart.setOption({
      tooltip: {
        trigger: "axis"
      },
      legend: {
        // data: this.nameList
      },
      grid: {
        left: '3%',
        // right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: "value",
        boundaryGap: [0.2, 0.2],
        max: 1400,
        min: 0,
      },
      yAxis: [
        {
          type: "category",
          data:this.nameList,
        },
      ],
      series: [
        {
          name: '',
          type: 'bar',
          data: this.totalList,
          label: {
                normal: {
                    show: true,
                    position: 'right'
                }
            },
        },
      ]
      });
    },
    //员工占比
    // percentEchart() {
    //     this.myChartPersend = echarts.init(document.getElementById("persendChart"));
    //     this.myChartPersend.setOption({
    //   tooltip: {
    //     trigger: "axis"
    //   },
    //   xAxis: [
    //     // type: "category",
    //     // data: []
    //     {
    //         type: 'category',
    //         boundaryGap: true,
    //         data:['一线','二线','三线']
    //     },
    //   ],
    //   grid: {
    //     left: '3%',
    //     right: '4%',
    //     bottom: '3%',
    //     containLabel: true
    // },
    //   yAxis: [
    //     {
    //     type: "value",
    //     boundaryGap: [0.2, 0.2],
    //     max: 8000,
    //     min: 0,
    //     },
    //     {
    //     type: "value",
    //     boundaryGap: [0.2, 0.2],
    //     max:100,
    //     min: 0,
    //     axisLabel: {
    //             formatter: '{value} %'
    //         }
    //     },
    //   ],
    //   series: [
    //     {
    //       name: '',
    //       type: 'bar',
    //       data: [400, 300, 100, 500, 600, 700],
    //     },
    //     {
    //       name: '',
    //       position: 'right',
    //       type: 'line',
    //       data: [40, 30, 10, 50, 60, 70],
    //     },
    //   ]
    //   });
    // },
  }
};



